import React from 'react';
import '../../themes/cardsDisplay.scss';
import { getDayOfWeek } from '../../helpFunctions';
import {Link} from 'react-router-dom';

export default ({temperature, text, date, city, locationId, link}) =>{


    return (
        <div className="card">
            {
                <div className="card-body">
                    <h3>{city}</h3>
                    <h6>({locationId})</h6>

                    {
                        date && <h4>{getDayOfWeek(date)}</h4>

                    }
                    <h5 className="card-title">{temperature}</h5>
                    <p className="card-text">{text}</p>
                    {
                        link && <Link to={`/main${link}`}><button className="btn btn-primary">See weather</button></Link>
                    }
                </div>
            }
            
        </div>
    )

}